@import url(https://fonts.googleapis.com/css2?family=Montserrat+Alternates:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
*{
  font-family: 'Montserrat Alternates', sans-serif;
  font-weight: 400;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.cardH{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 200px;
  height: 350px;
  background-color: #EEF3D2;
  border: #282c34 1px solid;
  border-radius: 10px;
  box-shadow: 2px 2px rgba(0, 0, 0, 0.452);
  margin-left: 10px;
}

.inputCard{
  width: 30px;
  text-align: center;
  border: none;
  background-color: #EEF3D2;
}

.btnCard{
  width: 25px;
  background-color: #947EC3;
  border: black 0.1px solid;
  color: #EEF3D2;
  font-weight: 500;
}

.styleNav{
  background-color: #947EC3;
}

.nav-link{
  color: #EEF3D2 !important;
  text-shadow: 1.5px 1.5px #00000050;
  font-size: 1.1rem !important;
}

.nav-link:hover{
  color: #d8fa17 !important;
  text-shadow: 1.5px 1.5px #00000050;
  font-size: 1.1rem !important;
  transition: 2s all;
}

.agregarItems{
  margin: 7px 5px;
}

.btnAddCar{
  background-color: #947EC3;
  border: black 0.1px solid;
  padding: 5px;
  color: #EEF3D2;
  font-weight: 500;
}

.imgProducto{
  border-radius: 10px;
  margin-top: 3px;
}

.titleInicio{
  background-color: #6A67CE;
  color: rgb(255, 255, 255);
}
* {
  /* Reset de margenes, bordes, etc.. */
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

* {
  /* Limpiamos márgenes y estilos provenientes de HTML */
  text-decoration: none;
  list-style-type: none;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.cantidadCompras{
  font-size: 0.8rem;
  position: fixed;
  background-color: rgb(204, 116, 0);
  color: white;
  padding: 0px 2px 0px 2px;
  margin-left: 20px;
  margin-top: 12px;
  border: rgba(255, 255, 255, 0.445) 1px solid;
  border-radius: 30px;
}


.titleInicio{
  text-align: center;
}
